* {
  box-sizing: border-box;
}

html, body {
  height: 100%;
}

html {
  scroll-behavior: smooth;
  font-size: 62.5%;
}

.content {
  padding-top: 5rem;
}

.landing__content {
  padding-top: 1rem;
  text-align: center;

  h3 {
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
    margin: 3rem 0 1rem;
    font-size: 2.7rem;
    font-weight: normal;
    color: $color-mine-shaft;
  }
  p {
    font-size: 1.3rem;
    padding: 0.5rem;
    color: $color-mine-shaft;
    line-height: 1.85rem;
  }
  a {
    color: $color-astral;
    text-decoration: none;
  }
}

